.article {
    background-color: #78c26e4f;
    width: 25%;
    height: 150mw;
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 40px;
    border-radius: 25px;
}

#boutique {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#boutique a {
    text-decoration: none;
    color: rgb(32, 80, 0);
}

#boutique a:hover {
    text-decoration: underline;
}

.img {
    display: flex;
    justify-content: center;
    align-items: center;
}

.iarticle {
    width: 65%;
    height: 100mw;
    margin-top: 20px;
    margin-bottom: 0px;
    border-radius: 25px;
}

.article p {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
}