#info {
    background-color: #6cb16357;
    margin-top: 15px;
    margin-left: 200px;
    margin-right: 200px;
    display: flex;
    color: rgb(12, 34, 8);
    border-radius: 25px;
}

#info img {
    margin: 50px;
    width: 300px;
    height: 300px;
    border-radius: 25px;
}

.text {
    margin-top: 50px;
}

h1 {
    font-size: 40px;
}

h2 {
    font-size: 30px;
}

.text p {
    font-weight: bold;
    margin-right: 50px;
}

#fbut {
    background-color: rgb(62, 153, 74);
    border-radius: 25px;
    color: #fff;
    font-weight: bold;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 400px;
}