#alert {
    color: #fff;
    background-color: #128116;
    font-family: 'New Amsterdam', sans-serif;
    display: flex;
    align-items: center; 
    justify-content: center; 
    height: 40px;
    font-size: 15px;
}

#alert a {
    text-decoration: underline;
    color: #fff;
}

#dev {
    height: 15px;
    width: 15px;
    color: #fff;
}

.sepa {
    height: 20px;
}

#titre {
    display: flex;
    align-items: center; 
    justify-content: center; 
    height: 55px;
}

#titre p {
    color: #128116;
    font-size: 50px;
    font-family: 'New Amsterdam', sans-serif;
    font-weight: bold;
    padding: 15px;
}

.topnav {
    background-color:  #fff; /* color #29e42e*/
    overflow: hidden;
    display: flex;  
    align-items: center; 
    justify-content: center; 
}

.topnav a {
    transition-duration: 250ms;
    color: #128116;
    float: left;
    text-align: center;
    padding: 10px;
    width: 25%;
    text-decoration: none;
    font-size: 17px;
    font-family: 'New Amsterdam', sans-serif;
    font-weight: bold;
}

.topnav img {
    height: 24px;
    width: 24px;
    padding: 20px 56px;
}


.topnav a:hover {
    color: #0b530d;
    text-decoration: underline;
    transition-duration: 250ms;
}

.topnav a:active {
    color: black;
}

.line {
    height: 1px;
    background-color: #1db422;
}